import React, { useEffect, useRef } from "react"

const IndexPage = () => {
  const video = useRef()

  useEffect(() => {
    navigator.mediaDevices.getUserMedia({
      video: true,
      audio: false
    })
      .then(stream => {
        video.current.srcObject = stream
      })
      .catch(err => {
        console.error(err)
      })
  }, [])

  return (
    <div>
      <p>見ているぞ！！！</p>
      <video
        ref={ref => video.current = ref}
        autoPlay
        playsInline
        muted
        width={480}
        height={640}
      />
    </div>
  )
}

export default IndexPage
